.TM_menu {
    width: 800px;
    margin-top: 100px;
    margin-left: 200px;
    height: 550px;
    padding-top: 20px;
    overflow-y: scroll;

    display: flex;
    flex-direction: column;
    justify-content: start;
 
    list-style: none;
    counter-reset: li;
}
.TM_menu>li {    
    font: 25px "Times New Roman";
    background-color: #D8E6F3;
    border-radius: 10px 10px 0 0;
    list-style-type: none;
    margin-left: 10px;
    padding-left: 50px;
}
.TM_menu>li:nth-child(odd) {
    background-color: #D8E6FF;
}
.TM_menu>li:hover {
    background-color: #D8E6AF;
}
.TM_menu>li{
    border: 2px solid #DDDDDD;
    background: #FAFAFA;
    position: relative;
    margin-bottom: 20px;
    padding: 16px 10px;
    -webkit-transition-duration: 0.4s;
    transition-duration: 0.4s;
}
.TM_menu>li:hover {
    z-index: 1;
    border: 2px solid #ADCEE9;
}
.TM_menu>li:before{
    border: 2px solid #ADCEE9;
    position: absolute;
    top: -14px;
    padding: 2px 10px;
    font-size: 21px;
    font-weight: bold;
    color: #337AB7;
    background: #F7FAFD;
    counter-increment: li;
    content: counter(li);
    -webkit-transition-duration: 0.3s;
    transition-duration: 0.3s;
}
.TM_menu>li:hover:before {
    border: 2px solid #337AB7;
    background: #337AB7;
    color: #FFF;
    -webkit-transform: translate(-12px, 0);
    -ms-transform: translate(-12px, 0);
    -o-transform: translate(-12px, 0);
    transform: translate(-12px, 0);
}
.TM_menu>li:after {
    content: "";
    position: absolute;
    -webkit-transition-duration: 0.4s;
    transition-duration: 0.4s;
    -webkit-transition-property: width;
    transition-property: width;
    z-index: -1;
    background: #F0F5FB;
    height: 100%;
    left: 0;
    top: 0;
    width: 0;
}
.TM_menu>li:hover:after {
    width: 100%;
}